import BaseApiService from "../../BaseApiService";

// ---------------- Default path ------------------ //
const COMMON_PATH = "/custom/api";
const DEFAULT_PATH = "/custom/api/login";
const IFSC_DEFAULT_PATH = "/custom/api/ifsc";

class BankAccountDetailsService extends BaseApiService {

  getAllIfsc = (start = 0, limit = 10, search = "") => {
    const url = `${IFSC_DEFAULT_PATH}/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };


  // ------------------------- To get bank account details list ----------------------------- //

  getBankAccountDetails = (loanId: string, applicantId: string) => {
    const endPoint = DEFAULT_PATH + `/${loanId}/${applicantId}/bankaccountdetail?isActive=true`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  // ------------------------- To create new bank account information ---------------- //

  createBankAccountDetail = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/dde/bankaccountdetail`;
    return this.makePostRequestWithAuth(endPoint, payload);
  };

  // --------------------------- To update the existing bank account details --------------------- //

  updateBankAccountDetail = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/dde/bankaccountdetail`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  // --------------------------- To search IFSC code for bank account details --------------------- //

  findByIFSCCode = (start = 0, limit = 10, search = "") => {
    const endPoint =
      IFSC_DEFAULT_PATH + `/search?ifscCode:lk=${search}&start=${start}&limit=${limit}`;
    return this.makeGetRequestWithAuth(endPoint);
  };


  // ------------------------- To get corporate bank account details list ----------------------------- //

  getCorporateBankAccountDetails = (loanId: string, applicantId: string) => {
    const endPoint = DEFAULT_PATH + `/${loanId}/${applicantId}/corporate/bankaccountdetail`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  // --------------------------- To update the existing corporate bank account details --------------------- //

  updateCorporateBankAccountDetail = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/dde/corporate/bankaccountdetail`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  // ------------------------- To create new corporate bank account information ---------------- //

  createCorporateBankAccountDetail = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/dde/corporate/bankaccountdetail`;
    return this.makePostRequestWithAuth(endPoint, payload);
  };

  // --------------------------- To search bank name --------------------- //

  findBankName = (start = 0, limit = 10, search = "") => {
    const endPoint = COMMON_PATH + `/bank/search?start=${start}&limit=${limit}&${search}`;
    return this.makeGetRequestWithAuth(endPoint);
  };
}
export default new BankAccountDetailsService();
